.modal-video {
    width: 100% !important;
    height: 100%;
    padding: 0;

    .ant-modal-content {
        height: 100%;
        background-color: #000 !important;

        .ant-modal-close {
            color: #FFF;

            span {
                font-size: 40px;
                margin-right: 20px;
                margin-top: 20px;
            }
        }

        .ant-modal-body {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            
            > div {
                width: 90% !important;
                height: 90% !important;
            }
        }
    }
}